import { createSelector } from "reselect";

const getHomeFromState = (state) => state.nav;

export const getNavLinkState = createSelector(
  [getHomeFromState],
  (state) => state.nav
);

export const getNavBrandState = createSelector(
  [getHomeFromState],
  (state) => state.brand
);

const getBlogFromState = (state) => state.blogs;

export const getBlogsState = createSelector(
  [getBlogFromState],
  (state) => state.blogs
);

const getPostDetailFromState = (state) => state.postReducer.postDetail.post;

export const getPostIdState = createSelector(
  [getPostDetailFromState],
  (post) => post
);


const getProgramsFromState = state => state.programs;

export const getProgramState =  createSelector(
  [getProgramsFromState],
  state => state.programs
)

const getLoader = state => state.global;

export const getLoaderState = createSelector(
  [getLoader],
  state => state.isloading
)

const getGalleryFromState = state => state.gallery;

export const getGalleryState = createSelector(
  [getGalleryFromState],
  state => state.gallery
)

export const getGalleryLoader = createSelector(
  [getGalleryFromState],
  state => state.isloading
)

const getEventFromState = state => state.eventReducer;

export const getEventState = createSelector(
  [getEventFromState],
  state => state.events
)

export const getEventLoader = createSelector(
  [getEventFromState],
  state => state.isloading
)