import React from 'react';
import PropTypes from 'prop-types';
import { GiHamburgerMenu } from 'react-icons/gi';
import { withRouter } from 'react-router-dom';
import classnames from 'classnames'
import logo from '../../../assets/logo.png';


const match = (path, link) => path === link;

const Navbar = (props) => {
  const { brand, navItem, location: {pathname} } = props;
 

  const navItems = navItem.map((item) => (
    <li className={classnames(match(pathname, item.link) ? 'navlink navlink-active' : 'navlink')} key={item.key}>
      <a  href={item.link}>{item.name}</a>
    </li>
  ));


  return (
    <div className='header-top-area'>
      <nav className='navbar navbar-expand-lg'>
        <div className='container'>
            <a className='navbar-brand' href='/'>
            <img src={logo} width="30" height="30" className="d-inline-block align-top mr-2" alt="" loading="lazy"/>
              {brand}
            </a>
          <button
            className='navbar-toggler'
            type='button'
            data-toggle='collapse'
            data-target='#navbarSupportedContent'
            aria-controls='navbarSupportedContent'
            aria-expanded='false'
            aria-label='Toggle navigation'>
            <GiHamburgerMenu className='text-white' />
          </button>
          <div className="collapse navbar-collapse justify-content-end" id="navbarSupportedContent">
            <ul className='nav navbar-nav pull-right'>{navItems}</ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

Navbar.propTypes = {
  brand: PropTypes.string.isRequired,
  navItem: PropTypes.arrayOf(PropTypes.shape({
    key: PropTypes.oneOfType([
      PropTypes.string.isRequired,
      PropTypes.number.isRequired
    ]),
    link: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired
  })),
  location: PropTypes.any
};

export default withRouter(Navbar);
