import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

const Banner = ({ image, title, body, center, height }) => {
  return (
    <div
      style={{ backgroundImage: `url(${image})`, height }}
      className='banner'>
      <div
        data-aos='fade-down'
        className='container d-flex flex-column caption-box justify-content-center position-relative'>
        <h2
          className={classnames(
            center ? 'banner-header mb-3 text-white text-center' : 'banner-header mb-3 app-text',
          )}>
          {' '}
          {title}{' '}
        </h2>
        <p
          className={classnames(
            center
              ? 'text-white text-center banner-text-white'
              : 'text-white banner-text-header',
          )}>
          {body}
        </p>
      </div>
    </div>
  );
};

Banner.defaultProps = {
  center: false,
  bodyCenter: false,
  height: '70vh',
};

Banner.propTypes = {
  image: PropTypes.string,
  title: PropTypes.string,
  body: PropTypes.string,
  center: PropTypes.bool,
  height: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
};

export default Banner;
