import React, { Component } from "react";
import { connect } from "react-redux";
import { getNavLinkState, getNavBrandState } from "../../store/reselect";
import slide2 from "../../assets/nurse.jpg";
import { FooterBrandBody } from '../../utils/data'
import View from "../../presentations/about";

class About extends Component {
  state = {
    title: "About",
    src: slide2,
    footerBrandBody:
      "CRUXCOHTECH is a registered under Coporate Affairs Commission[RC 1406451] and approved by the Ogun state ministry of education, science and technology",
  };
  render() {
    let {
      state: { title, body, src, },
      props: { brand, navlinks },
    } = this;
    return (
      <View
        headerBody={body}
        headerTitle={title}
        navlinks={navlinks}
        footerBrandBody={FooterBrandBody}
        imageUri={src}
        navbrand={brand}
      />
    );
  }
}

const mapStateToProps = (state) => ({
  brand: getNavBrandState(state),
  navlinks: getNavLinkState(state)
});

const mapDispatchToProps = (dispatch) => {
  return {
    onDispatch: () => {
      dispatch();
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(About);
